$watchSize: 500px; // watch size - only size that need setup

$minutesWidth: $watchSize / 100 * 3;
$secondsWidth: $watchSize / 100 * 2;

$offsetMin: calc(50% - #{$minutesWidth} / 2);
$offsetSec: calc(50% - #{$secondsWidth} / 2);

